import PikachuImage from "../PokemonPictures/PikachuLarge.jpg"
import EeveeImage from "../PokemonPictures/EeveeLarge.jpg"
import GengarImage from "../PokemonPictures/GengarLarge.jpg"
import OnixImage from "../PokemonPictures/OnixLarge.jpg"
import CharizardImage from "../PokemonPictures/CharizardLarge.jpg"
import MewtwoImage from "../PokemonPictures/MewtwoLarge.jpg"
import React, { Component} from 'react'
import Container from 'materialize-css'
import { Link } from 'react-router-dom'

class ChooseYourFighter extends Component{


render(){

    return(

<div class="container">
<h1 id="pokedexTitle">Choose Your Fighter</h1>
    <div class="row">
        <div class="col s6">
            <div class="card" id="cardStyling">
                <div class="card-image">
                <Link to="/Battle">    
                    <img id="chooseimg"src= {PikachuImage}></img>
                </Link>
                   
          
                </div>
                <div class="card-content">
                    <p id="pokemonSelectText">Pikachu</p>
                </div>

            </div>
        </div>
      <div class="col s6">
      <div class="card" id="cardStyling">
                <div class="card-image">
                <Link to="/Battle">    
                    <img id="chooseimg"src= {CharizardImage}></img>
                </Link>
                    
          
                </div>
                <div class="card-content">
                    <p id="pokemonSelectText">Charizard</p>
                </div>

            </div>
      </div>
    </div>
    <div class="row">
      <div class="col s6">
      <div class="card"id="cardStyling" >
                <div class="card-image">
                <Link to="/Battle">    
                    <img id="chooseimg"src= {GengarImage}></img>
                </Link>
          
                </div>
                <div class="card-content">
                    <p id="pokemonSelectText">Gengar</p>
                </div>

            </div>
      </div>
      <div class="col s6">
      <div class="card" id="cardStyling" >
                <div class="card-image">
                <Link to="/Battle">    
                    <img id="chooseimg"src= {OnixImage}></img>
                </Link>
                    
          
                </div>
                <div class="card-content">
                    <p id="pokemonSelectText">Onix</p>
                </div>

            </div>
      </div>
    </div>
    <div class="row">
      <div class="col s6">
      <div class="card" id="cardStyling">
                <div class="card-image">
                <Link to="/Battle">    
                    <img id="chooseimg"src= {EeveeImage}></img>
                </Link>
                    
          
                </div>
                <div class="card-content">
                    <p id="pokemonSelectText">Eevee</p>
                </div>

            </div>
      </div>
      <div class="col s6">
      <div class="card" id="cardStyling" >
                <div class="card-image">
                <Link to="/Battle">    
                    <img id="chooseimg"src= {MewtwoImage}></img>
                </Link>
          
                </div>
                <div class="card-content">
                    <p id="pokemonSelectText">Mewtwo</p>
                </div>

            </div>
      </div>
     
    </div>
</div>

    )
    
    }
}

export default ChooseYourFighter;

